.contact{
    display: flex;
    flex-direction: column;
    text-align: left;
    width: 70%;
    margin: auto;
    background-color: black;
    padding: 4rem;
    color: aliceblue;
    font-family: 'Chivo Mono', monospace;
    border-radius: 2rem;
}


.contact-form{
    font-family: 'Chivo Mono', monospace;
    margin-top: 4rem ;
}


.contact input{
    border: none;
    border-radius: 1rem;
    padding: 1rem;
}

.contact textarea{
    border: none;
    border-radius: 1rem;
    padding: 1rem;
}


#FAQ{
    border-radius: 5rem!important;
    margin: 5rem 0rem;
}