@import url('https://fonts.googleapis.com/css2?family=Chivo+Mono&display=swap');

body{
    background-color: antiquewhite;
}

.home{
    font-family: 'Chivo Mono', monospace;
    padding: 5rem;
}

.home img{
    height: 25rem;
}

.why{
    font-family: 'Chivo Mono', monospace;
    background-color: #fff;
    padding:3rem 0rem;
    /* width: 60rem; */
    border-radius: 3rem;
    margin: 3rem auto;
    
}

.Why{
    text-align: left;
    padding: 0rem 20%;
    font-family: 'Chivo Mono', monospace;
}

.services {
  font-family: 'Chivo Mono', monospace;
}

.card img{
    width: 8rem;
    text-align: center;
    margin: auto;
    
}

.card{
    padding: 1rem;
    margin: 1rem;
    width: 17rem;
    border-radius: 5rem;
}

.card:hover{
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem #000;
}

.progress-container {
  width: 100%;
  height: 8px;
  background: #f1f1f1;
  position: fixed;
  z-index: 10;
}

.progress-bar {
  height: 100%;
  background: #2c68ff;
  width: 0;
}

.recycling-process {
    font-family: 'Chivo Mono', monospace;
    border-radius: 1rem;
    max-width: 800px;
    margin: 5rem auto;
    padding: 40px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .recycling-step {
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;
  }
  
  .step-number {
    /* width: 3rem; */
    height: 3rem;
    font-size: 2rem;
    padding: 1rem;
    font-weight: bold;
    color: #fff;
    background-color: #0072c6;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 20px;
  }
  
  .step-description h3 {
    margin-top: 0;
    margin-bottom: 10px;
    font-size: 24px;
  }
  
  .step-description p {
    margin-top: 0;
    margin-bottom: 0;
    font-size: 18px;
    line-height: 1.5;
  }
  