.blog{
    display: flex;
    flex-direction: column;
    color: rgb(0, 0, 0);
    text-align: left;
    border: #000 2px solid;
    margin: 1rem 0rem;
    padding: 1rem;
    border-radius: 2rem;
    background-color: aliceblue;
}

.blog:hover{
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem #000;
}

.title{
    color: red;
    margin-bottom: 0.5rem;
}
.date {
    color: red;
    margin-bottom: 2rem;

}
.Author{
    color: red;
    margin-bottom: 0.5rem;
}

.blog-list{
    font-family: 'Chivo Mono', monospace;
}